.Container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: calc(100vh - 0px);
  /* border: 7px solid #ffc900; */

}

.Wrapper{
  max-width: 1400px;
  width: 100%;
  position: relative;
  z-index: 9;
}

.Container:before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background: url(../../assets/ludo_banner.jpg);
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: right;
  opacity: 0.2;
}

.Container:after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background: url(../../assets/ludo_banner.jpg);
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: left;
  opacity: 0.2;
  transform: rotate(180deg);
}

.GameContainer {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: space-between;
  position: relative;
  z-index: 9;
  max-width: 1400px;
  width: 100%;
}

.Board {
  /* border: 1px solid #CCCCCC; */
  display: flex;
  flex-wrap: wrap;
  border-radius: 20px;
  overflow: hidden;
}

.PlayerContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0px;
  max-width: 300px;
  width: 100%;
}

.playerRight .playerWrapper {
  border-radius: 50px 10px 10px 50px;
}

.GameConfiguration {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 750px;
  margin: 0px auto;
  width: 100%;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 50%;
  z-index: 9;
}

.GameConfiguration button {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  font-size: 16px;
  font-family: 'Bungee', cursive;
}
.GameConfiguration button.playbtn {
  color: #000;
  text-transform: uppercase;
  padding: 10px 20px;
  width: 32%;
  background: #fff;
  border: 2px solid #ddd;
  border-radius: 0.75em;
  transform-style: preserve-3d;
  transition: transform 150ms cubic-bezier(0, 0, 0.58, 1), background 150ms cubic-bezier(0, 0, 0.58, 1);
}
.GameConfiguration button.playbtn::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #ddd;
  border-radius: inherit;
  box-shadow: 0 0 0 2px #ddd, 0 0em 0 0 #ffe3e2;
  transform: translate3d(0, 0.75em, -1em);
  transition: transform 150ms cubic-bezier(0, 0, 0.58, 1), box-shadow 150ms cubic-bezier(0, 0, 0.58, 1);
}
.GameConfiguration button.playbtn:hover {
  background: #fff;
  transform: translate(0, 0.25em);
}
.GameConfiguration button.playbtn:hover::before {
  /* box-shadow: 0 0 0 2px #b18597, 0 0.5em 0 0 #ffe3e2; */
  transform: translate3d(0, 0.5em, -1em);
}
.GameConfiguration button.playbtn:active {
  background: #fff;
  transform: translate(0em, 0.75em);
}
.GameConfiguration button.playbtn:active::before {
  /* box-shadow: 0 0 0 2px #b18597, 0 0 #ffe3e2; */
  transform: translate3d(0, 0, -1em);
}

.logo{
  position: absolute;
  bottom: 20px;
  left: 20px;
  z-index: 9;
  filter: drop-shadow(2px 4px 6px black);
}

.logo img {
  width: 80px;
}


