.Container {
  border-radius: 50% 50% 50% 0;
  transform: rotate(-45deg);
  border: 1px solid #0000001a;
  margin-bottom: 50%;
  filter: contrast(1.2);
  box-shadow: 0px 1px 5px #0000003b;
  background: #c7c7c7;
  background: linear-gradient(224deg, rgb(152 148 148) 0%, rgb(255 255 255) 100%);
}

.Container span {
  content: '';
  width: 20px!important;
  height: 20px!important;
  margin: 25% 0 0 25%;
  background: #FFFFFF;
  position: absolute;
  border-radius: 50%;
  box-shadow: inset -2px 2px 3px #00000054;
}
