

.playerBox{
    max-width: 150px;
    width: 100%;
}

.playerPic {
  width: 150px;
    height: 150px;
    border-radius: 100%;
    background: #0f1524;
    overflow: hidden;
}
.playerPic img{
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: 100%;
    
}


.playerInfo h5{
  margin: 0%;
  text-align: center;
  font-family: 'Bungee', cursive;
  font-weight: 400;
  color: #fff!important;
  line-height: 1.2;
  padding-top: 10px;
  letter-spacing: 1px;
}
